"use client";

import { useEffect, useState } from "react";
import axiosInstance from "@/../ultis/axios";

type Update = { _id: string; content: string; createdAt: string };
type ActivityLog = { _id: string; action: string; createdAt: string; metadata?: any };
type FileDoc = { _id: string; title: string; file_url: string; createdAt: string };

function Section({ label, children }: { label: string; children: React.ReactNode }) {
  return (
    <div>
      <div className="text-xs uppercase tracking-wide opacity-60 mb-1">{label}</div>
      {children}
    </div>
  );
}

export default function ModalMilestone({ open, onClose, projectId, milestoneId }: { open: boolean; onClose: () => void; projectId: string; milestoneId: string; }) {
  const [tab, setTab] = useState<"updates"|"files"|"activity">("updates");
  const [updates, setUpdates] = useState<Update[]>([]);
  const [files, setFiles] = useState<FileDoc[]>([]);
  const [activity, setActivity] = useState<ActivityLog[]>([]);
  const [content, setContent] = useState("");
  const [loading, setLoading] = useState(false);
  const [posting, setPosting] = useState(false);
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [status, setStatus] = useState("Planned");
  const [startDate, setStartDate] = useState("");
  const [deadline, setDeadline] = useState("");
  const [saving, setSaving] = useState(false);

  useEffect(() => {
    if (!open) return;
    (async () => {
      setLoading(true);
      try {
        const [m, u, f, a] = await Promise.all([
          axiosInstance.get(`/api/projects/${projectId}/milestones/${milestoneId}`),
          axiosInstance.get(`/api/projects/${projectId}/milestones/${milestoneId}/updates`),
          axiosInstance.get(`/api/projects/${projectId}/milestones/${milestoneId}/files`),
          axiosInstance.get(`/api/projects/${projectId}/milestones/${milestoneId}/activity-logs`),
        ]);
        const md = m.data || {};
        setTitle(md.title || "");
        setDescription(md.description || "");
        setStatus(md.status || "Planned");
        setStartDate(md.start_date ? md.start_date.substring(0,10) : "");
        setDeadline(md.deadline ? md.deadline.substring(0,10) : "");
        setUpdates(Array.isArray(u.data) ? u.data : []);
        setFiles(Array.isArray(f.data) ? f.data : []);
        setActivity(Array.isArray(a.data) ? a.data : []);
      } finally {
        setLoading(false);
      }
    })();
  }, [open, projectId, milestoneId]);

  const submitUpdate = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!content.trim()) return;
    setPosting(true);
    try {
      const res = await axiosInstance.post(`/api/projects/${projectId}/milestones/${milestoneId}/updates`, { content });
      setUpdates(prev => [res.data, ...prev]);
      setContent("");
    } finally {
      setPosting(false);
    }
  };

  if (!open) return null;
  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center">
      <div className="absolute inset-0 bg-black/40 backdrop-blur-sm" onClick={onClose} />
      <div className="relative w-full max-w-5xl max-h-[82vh] rounded-2xl border overflow-hidden shadow-[0_10px_30px_rgba(0,0,0,0.2)]" style={{borderColor:'var(--border)', background:'color-mix(in oklab, var(--accent) 18%, var(--background))'}}>
        <div className="flex items-center justify-between px-5 py-4 border-b bg-[color-mix(in_olab,_var(--background)_75%,_transparent)]" style={{borderColor:'var(--border)'}}>
          <div className="font-semibold text-lg tracking-tight">{title || "Item"}</div>
          <button className="text-foreground/70 hover:text-foreground transition-colors" onClick={onClose}>✕</button>
        </div>
        <div className="flex" style={{minHeight: '52vh'}}>
          {/* Left details panel */}
          <aside className="w-[300px] border-r p-4 space-y-4 bg-[color-mix(in_olab,_var(--accent)_8%,_var(--background))]" style={{borderColor:'var(--border)'}}>
            
            <Section label="Name">
              <input value={title} onChange={(e)=>setTitle(e.target.value)} className="w-full border rounded-lg p-2.5 transition-shadow bg-[var(--background)] text-foreground focus:outline-none focus:ring-2 focus:ring-[var(--ring)]" style={{borderColor:'var(--border)'}} />
            </Section>
          
            <Section label="Status">
              <select value={status} onChange={(e)=>setStatus(e.target.value)} className="w-full border rounded-lg p-2.5 bg-[var(--background)] text-foreground transition-shadow focus:outline-none focus:ring-2 focus:ring-[var(--ring)]" style={{borderColor:'var(--border)'}}>
                <option>Planned</option>
                <option>In Progress</option>
                <option>Completed</option>
                <option>Overdue</option>
              </select>
            </Section>
            <Section label="Due date">
              <input type="date" value={deadline} onChange={(e)=>setDeadline(e.target.value)} className="w-full border rounded-lg p-2.5 transition-shadow bg-[var(--background)] text-foreground focus:outline-none focus:ring-2 focus:ring-[var(--ring)]" style={{borderColor:'var(--border)'}} />
            </Section>
            <Section label="Notes">
              <textarea value={description} onChange={(e)=>setDescription(e.target.value)} rows={2} className="w-full border rounded-lg p-2.5 transition-shadow bg-[var(--background)] text-foreground focus:outline-none focus:ring-2 focus:ring-[var(--ring)]" style={{borderColor:'var(--border)'}} />
            </Section>
            <Section label="Files">
              <div className="flex items-center gap-2"><span className="inline-block h-4 w-4 bg-gray-300 rounded" /> {files.length}</div>
            </Section>
            <Section label="Timeline">
              <div className="text-sm text-gray-600">{startDate || '—'} - {deadline || '—'}</div>
              <div className="grid grid-cols-2 gap-2 mt-2">
                <input type="date" value={startDate} onChange={(e)=>setStartDate(e.target.value)} className="border rounded-lg p-2.5 transition-shadow bg-[var(--background)] text-foreground focus:outline-none focus:ring-2 focus:ring-[var(--ring)]" style={{borderColor:'var(--border)'}} />
                <input type="date" value={deadline} onChange={(e)=>setDeadline(e.target.value)} className="border rounded-lg p-2.5 transition-shadow bg-[var(--background)] text-foreground focus:outline-none focus:ring-2 focus:ring-[var(--ring)]" style={{borderColor:'var(--border)'}} />
              </div>
            </Section>
            <Section label="Last updated">
              <div className="text-sm opacity-70">{activity[0]?.createdAt ? new Date(activity[0].createdAt).toLocaleString() : '—'}</div>
            </Section>
            <div className="flex justify-end pt-2">
              <button disabled={saving} onClick={async ()=>{ setSaving(true); try { await axiosInstance.patch(`/api/projects/${projectId}/milestones/${milestoneId}`, { title, description, status, start_date: startDate ? new Date(startDate).toISOString() : undefined, deadline: deadline ? new Date(deadline).toISOString() : undefined }); } finally { setSaving(false); } }} className="btn-primary px-4 py-2 rounded-lg">Save</button>
            </div>
          </aside>

          {/* Right content tabs */}
          <section className="flex-1 p-5">
            <div className="flex items-center justify-between">
              <nav className="flex gap-2 bg-[color-mix(in_olab,_var(--accent)_16%,_var(--background))] rounded-xl p-1 border" style={{borderColor:'var(--border)'}}>
                {([['updates','Updates', updates.length], ['files','Files', files.length], ['activity','Activity Log', activity.length]] as any[]).map(([k,l,c]) => (
                  <button key={k} className={`px-3 py-1.5 rounded-lg text-sm transition-colors ${tab===k?"bg-[var(--background)] shadow text-[var(--primary)]":"text-foreground/70 hover:text-foreground"}`} onClick={()=>setTab(k as any)}>{l}{typeof c==='number' ? ` / ${c}` : ''}</button>
                ))}
              </nav>
              <div className="text-sm opacity-70 flex items-center gap-4">
                <span>Update via email</span>
                <span>Give feedback</span>
              </div>
            </div>

            <div className="mt-3 max-h-[44vh] overflow-y-auto">
              {loading ? <div>Loading...</div> : (
                <>
                  {tab === 'updates' && (
                    <div className="space-y-4">
                      <form onSubmit={submitUpdate} className="border rounded-xl p-4 bg-[color-mix(in_olab,_var(--background)_85%,_transparent)]" style={{borderColor:'var(--border)'}}>
                        <div className="flex items-center gap-3 text-sm text-gray-500 mb-2">
                          <span className="px-2 py-1 rounded bg-gray-100">@</span>
                          <span className="px-2 py-1 rounded bg-gray-100">GIF</span>
                          <span className="px-2 py-1 rounded bg-gray-100">🙂</span>
                          <span className="px-2 py-1 rounded bg-gray-100">✦</span>
                        </div>
                        <textarea className="w-full border rounded-lg p-3 transition-shadow bg-[var(--background)] text-foreground focus:outline-none focus:ring-2 focus:ring-[var(--ring)]" style={{borderColor:'var(--border)'}} rows={4} placeholder="Write an update and mention others with @" value={content} onChange={(e)=>setContent(e.target.value)} />
                        <div className="flex justify-end gap-2 mt-2">
                          <button type="button" className="px-3 py-2 rounded-lg text-sm border hover:bg-[color-mix(in_olab,_var(--accent)_14%,_transparent)]" style={{borderColor:'var(--border)'}} onClick={()=>setContent("")}>Cancel</button>
                          <button disabled={posting} className="btn-primary px-4 py-2 rounded-lg">Update</button>
                        </div>
                      </form>
                      {updates.length === 0 && <div className="opacity-70 text-sm text-center py-8">No updates yet</div>}
                      <div className="space-y-3">
                        {updates.map(u => (
                          <div key={u._id} className="border rounded-lg p-3 bg-[color-mix(in_olab,_var(--accent)_8%,_var(--background))]" style={{borderColor:'var(--border)'}}>
                            <div className="text-xs opacity-70">{new Date(u.createdAt).toLocaleString()}</div>
                            <div className="mt-1">{u.content}</div>
                          </div>
                        ))}
                      </div>
                    </div>
                  )}
                  {tab === 'files' && (
                    <div className="space-y-3">
                      {files.length === 0 && <div className="opacity-70 text-sm">No files</div>}
                      {files.map(f => (
                        <div key={f._id} className="flex items-center justify-between border rounded-lg p-3 bg-[color-mix(in_olab,_var(--accent)_8%,_var(--background))]" style={{borderColor:'var(--border)'}}>
                          <div>
                            <div className="font-medium">{f.title}</div>
                            <div className="text-xs opacity-70">{new Date(f.createdAt).toLocaleString()}</div>
                          </div>
                          <a className="text-[var(--primary)]" href={f.file_url} target="_blank" rel="noreferrer">Open</a>
                        </div>
                      ))}
                    </div>
                  )}
                  {tab === 'activity' && (
                    <div className="space-y-3">
                      {activity.length === 0 && <div className="opacity-70 text-sm">No activity</div>}
                      {activity.map(a => (
                        <div key={a._id} className="border rounded-lg p-3 bg-[color-mix(in_olab,_var(--accent)_8%,_var(--background))]" style={{borderColor:'var(--border)'}}>
                          <div className="text-xs opacity-70">{new Date(a.createdAt).toLocaleString()}</div>
                          <div className="mt-1 font-medium">{a.action}</div>
                        </div>
                      ))}
                    </div>
                  )}
                </>
              )}
            </div>
          </section>
        </div>
      </div>
    </div>
  );
}


